










import Vue from 'vue'
import { Component, Prop } from "vue-property-decorator"

import CounterService from "@/services/CounterService"

@Component
export default class ZaehlstelleHeader extends Vue {

  @Prop() readonly counterId!: string

  // Zhlungen
  zaehlung: string = "24.12.2019 Standardzhlung"
  items: string [] = ["24.12.2019 Standardzhlung", "16.04.2018 Querschnittzhlung", "12.08.2015 Standardzhlung"]


  get counter() {
    return CounterService.counterById(this.counterId)
  }

}
